.imageBlock {
  display: flex;
  gap: 8px; /* Reduced gap for a tighter look */
  padding: 8px;
  justify-content: center;
}

/* Adjust column width for smaller images */
.column {
  display: flex;
  flex-direction: column;
  gap: 8px;
  flex: 1 1 120px; /* Adjusted base width to make images even smaller */
}

.imageItem {
  overflow: hidden;
  border-radius: 6px; /* Slightly smaller border radius */
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1); /* Slightly lighter shadow */
}

.imageItem img {
  width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

/* Responsive adjustments */
@media (max-width: 1200px) {
  .imageBlock {
    gap: 6px;
  }
  .column {
    gap: 6px;
    flex: 1 1 100px; /* Smaller on medium screens */
  }
}

@media (max-width: 768px) {
  .imageBlock {
    flex-direction: column;
    gap: 5px;
  }
  .column {
    gap: 5px;
    flex: 1 1 80px; /* Even smaller on mobile */
  }
}
