@media (max-width: 500px) {
  .container {
    margin-left: 5px;
    margin-top: 30px;
    overflow: hidden;
  }
  .details {
    width: 100%;
    justify-content: left;
    flex-direction: column;
  }
}

@media (max-width: 926px) and (min-width: 500px) {
  .details {
    flex-direction: column;
    gap: 30px;
  }
}
@media (max-width: 1168px) and (min-width: 926px) {
  .details {
    /* padding: 0; */
    flex-direction: column;
    gap: 30px;
  }
}

@media (max-width: 1380px) and (min-width: 1168px) {
  .details {
