/* ImageGallery.module.css */

.imageBlock {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 10px; /* Base height for each row */
  gap: 10px;
  padding: 10px;
}

/* Base styles for each image item */
.imageItem {
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
}

/* Image styling to fit container */
.imageItem img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}
