/* Default styling for larger screens */
.items {
  display: flex;
  gap: 65px;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 50px;
  justify-content: center;
}

/* Semi-large screens (1280px) centered grid */
@media (max-width: 1280px) {
  .items {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 30px;
    justify-content: center;
  }
}

/* Medium screens: adjust the grid layout */
@media (max-width: 768px) {
  .items {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 30px;
    justify-content: center;
  }
}

/* Small screens: compact layout */
@media (max-width: 500px) {
  .items {
    display: grid;
    grid-template-columns: repeat(2, 120px);
    justify-content: center;
    gap: 15px;
    margin-bottom: 30px;
  }
}
